<template>
    <div class="detailcollection">
        <div class="row">
            <div class="col-xs-12 col-md-12">
                <div v-if="collection" class="collection-detail-display">
                    <!-- Render your collection details here -->
                    <h1>{{ collection?.name }}</h1>
                    <p>{{ collection?.description }}</p>

                    <h4><a :href="collection?.link" target="_blank">{{ $t('collectionDetailDownload') }}</a></h4>

                    <!-- making slide photo slideshow -->

                    <carousel :items-to-show="1.5">
                        <slide v-for="(image, index) in images" :key="index" class="carousel-slide">
                            <img :src="image.image" :alt="image.description" style="width:80%">
                        </slide>

                        <template #addons>
                            <navigation />
                            <pagination />
                        </template>
                    </carousel>

                </div>
                <div v-else>
                    <h1>404 - Collection Not Found</h1>
                    <p>The collection you are looking for does not exist.</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import i18n from '@/i18n/i18n';
import 'vue3-carousel/dist/carousel.css'
import { Carousel, Slide, Pagination, Navigation } from 'vue3-carousel'

export default {    
    name: 'DetailCollection',
    data() {
        return {
            id: 0,
            collection: {},
            collections: [],
            images: [],
            index: null
        }
    },
    components: {
        Carousel,
        Slide,
        Pagination,
        Navigation,
    },

    props: {

    },
    methods: {
        fetchCollectionDetail(id, locale) {
            // Implement your fetch collection logic here
            const collection = this.collections.find(collection => collection.id == id);

            console.log(collection);

            // find translation for name and description
            if (collection) {
                const translation = collection.translation.find(translation => translation.locale == locale);
                console.log(translation);
                if (translation) {
                    collection.name = translation.name;
                    collection.description = translation.description;

                    console.log(translation);
                }
            }

            if (collection) {
                return collection;
            }
            return null;
        }

    },
    mounted() {
        const collectionsGet = require(`@/assets/data/collection_international.json`);
        this.collections = collectionsGet.data;

        // Implement your logic here
        this.id = this.$route.params.id;

        const collection = this.fetchCollectionDetail(this.id, i18n.locale);

        // Implement your logic here
        this.collection = collection;

        // convert those object to string array only
        this.images = this.collection?.list_image;

        this.$watch(() => i18n.global.locale, (newLocale, oldLocale) => {
            const collection = this.fetchCollectionDetail(this.id, newLocale);
            this.collection = collection;
        });
    }
}
</script>

